#page1{
    height: 100vh;
    width: 100%;
    background-color: #111;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

}

#page1 h1{
    font-size: 8.5vw;
    text-transform: uppercase;
    font-weight: 900;
    line-height: 8.5vw;
}

#page1 h1 svg{
    height: 6vw;
    width: 6vw;
    background-color: #0CA34E;
    border-radius: 50%;
}

#page1 p{
    text-align: center;
    margin-top: 3vh;
}

#page1-some{
    display: flex;
    align-items: center;
    justify-content: center;
}
#page1-some h4{
    font-size: 1vw;
    font-weight: 400;
    padding: 5px 12px;
    border: 1px solid #a5a5a5;
    border-radius:50px ;
    margin-top: 1vw;
}

#page1-some h4:nth-child(3){
    border: none;
}

.moving-div{
    /* background-color: red; */
    position: absolute;
    bottom: 5%;
    width: 50%;
    white-space: nowrap;
    padding: 2vw;
    overflow: hidden;
}
.moving-div .move{
    display: inline-block;
    animation: move 15s ;
    animation-timing-function: linear;
    animation-iteration-count: infinite;

}

.moving-div .move img{
    height: 2vh;
    margin: 0 1vw ;
}

@keyframes move {
    0%{
        transform: translateX(0);
    }
    100%{
        transform: translateX(-100%);
    }
}

.blur-left{
    height: 100%;
    width: 20%;
    position:sticky;
    top: 0;
    left: 0;
    z-index: 1;
    background: linear-gradient(to left, transparent, #111);
}
.blur-right{
    height: 100%;
    width: 20%;
    position: static;
    top: 0;
    right: 0;
    z-index: 1;
    background: linear-gradient(to right, transparent, #111);

}

@media screen and (max-width:768px) {
    #page1{
        height: 88vh;
    }
    #page1 h1 {
        font-size: 10vw; /* Increase font size for better readability */
        line-height: 10vw; /* Adjust line height */
      }

      #page1 h1 svg {
        height: 8vw; 
        width: 8vw;
      }
      #page1 p {
        font-size: 3.3vw; 
        margin-top: 2vh; 
      }
      #page1-some h4 {
        font-size: 2.8vw;
        padding: 8px 6px; 
        margin-top: 2vh; 
      }    
      .moving-div {
        width: 80%; 
        bottom: 20%; 


      }
    
      .moving-div .move img {
        height: 2.4vh; /* Increase image size */
        margin: 0 2vw; /* Adjust margin */
      }
      .blur-left,.blur-right{
        display: none;
      }
}