    background: url('/Users/jumanahmaheen/ReactJS/project/frontend/src/components/assets/wave.svg') no-repeat center center;
    background-size: cover;

  } */
  
  .footer-content {
    position: relative;
    z-index: 1;
  }
  
  .social-icons {
    margin-bottom: 20px;
  }
  
  .social-icons a {
    color: white;
    margin: 0 10px;
    font-size: 20px;
  }
  
  .footer-links {
    margin-bottom: 20px;
  }
  
  .footer-links a {
    color: white;
    margin: 0 15px;
    text-decoration: none;
  }
  
  .footer-links a:hover {
    text-decoration: underline;
  }
  
  @media (max-width: 600px) {
    .footer-links a {
      display: block;
      margin: 5px 0;
    }
  }
  
  @media (max-width: 768px) {
    .footer-links {
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 10px;
    }
  
    .footer-links a {
      display: block;
      margin: 5px 0;
    }
  
    .social-icons {
      gap: 15px;
    }
  }