/* Prevent horizontal scrolling */
html, body {
  overflow-x: hidden; /* Hide horizontal scrollbar */
  width: 100%; /* Ensure the body takes full width */
  max-width: 100%; /* Ensure the body doesn't exceed the viewport width */
}


nav {
  position: fixed;
  display: flex;
  width: 100%;
  max-width: 100%; /* Ensure the nav doesn't exceed the viewport width */
  box-sizing: border-box;
  align-items: flex-start;
  justify-content: space-between;
  padding: 2vw 7vw;
  background-color: #111; 
  height: 12vh ;
  z-index: 10;
}
nav h1{
    font-weight: 900;
}
.nav-part2 {
  display: flex;
  gap: 2vw;
  align-items: flex-start;
  padding: 1vh 0;
  position: relative;
  z-index: 10;
}
.nav-part2 h4 {
  font-size: 0.8vw;
  font-weight: 600;
  text-transform: uppercase;
  margin-bottom: 2.6vw;
}
.nav-part2 h5 {
  margin-bottom: 1vw;
  display:none;
  overflow: hidden;
  /* background-color: red; */
}
.nav-part2 h5 span {
  font-size: 0.77vw;
  font-weight: 400;
  display: inline-block;
  transform: translateY(25px);
}
nav button {
  background-color: #0ca34e;
  display: flex;
  align-items: center;
  padding: 3px 12px;
  border-radius: 50px;
  font-size: 0.9vw;
  font-weight: 700;
  border: none;
  text-transform: uppercase;
}
nav button svg {
  height: 1.5vw;
  margin-right: 1vw;
}

#nav-bottom{
    width: 86%;
    height: 0vh;
    position: absolute;
    background-color: #111;
    top: 100%;
    z-index: 9;
    border-bottom: 1px solid #dadada ;
} 
.nav-elem h4{
    cursor: pointer;
}
nav a{
  text-decoration: none;
  font-size: small;
}

/* Mobile Responsive Styles */
@media screen and (min-width: 768px){
  .hamburger{
    display: none;
  }
  
}

@media screen and (max-width: 768px) {
  nav {
    flex-direction: column;
    align-items: center;
    padding: 4vw 5vw;
    height: auto;
    position: relative;
  }

  .hamburger {
    display: block;
    cursor: pointer;
    position: absolute;
    top: 10px;
    right: 20px;
    font-size: 5vw;
    z-index: 1001;
    color: white;
  }
  .lets-connect{
    display: none;
  }
  .nav-overlay {
    position: fixed;
    top: 0;
    right: -100%;
    width: 100%;
    height: 100vh;
    background: rgba(0, 0, 0, 0.8);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: right 0.3s ease-in-out;
    z-index: 1000;
  }

  .nav-overlay.open {
    right: 0;
  }

  .close-btn {
    position: absolute;
    top: 10px;
    right: 20px;
    font-size: 6vw;
    cursor: pointer;
    color: white;
    display: none;
  }

  .nav-overlay.open .close-btn {
    display: block;
  }

  .nav-overlay .nav-part2 {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2vw;
  }

  .nav-part2 h4 {
    font-size: 5vw;
    margin-bottom: 3vw;
    color: white;
  }

  .nav-part2 h5 {
    font-size: 4vw;
    margin-bottom: 2vw;
    color: white;
  }

  nav button {
    font-size: 4vw;
    padding: 8px 22px;
  }

  nav button svg {
    height: 5vw;
    margin-right: 3vw;
  }

  #nav-bottom {
    width: 100%;
  }

  @media screen and (min-width: 769px) {
    .hamburger {
      display: none;
    }
  }
}