.page2 {
    height: 100vh;
    width: 100%;
    position: relative;
    background-color: #111;
    padding: 0vw 10.5vw;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-top: 1px solid #333;
  }
  
  #left-div {
    height: 100%;
    width: 40%;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    flex-direction: column;
    padding: 2.5vw 0;
  }
  
  #left-div p {
    font-size: 1vw;
    font-weight: 450;
    width: 50%;
  }
  
  #left-div h5 {
    font-size: 1.2vw;
    font-weight: 400;
    width: 80%;
    padding-bottom: 13vh;
  }
  
  #right-div {
    height: 100%;
    width: 50%;
    cursor: pointer;
  }
  
  .right-elem {
    position: relative;
    border-top: 1px solid #333;
  }
  
  .right-elem h4 {
    font-size: 1.6vw;
    font-weight: 400;
    padding-top: 2.5vh;
    width: 80%;
  }
  
  .right-elem img {
    height: 5vw;
    width: 5vw;
    border-radius: 50%;
    object-fit: cover;
    opacity: 0;
    scale: 0;
  }
  
  /* Mobile Responsive Styles */
  @media screen and (max-width: 768px) {
    .page2 {
      flex-direction: column; /* Stack left and right divs vertically */
      padding: 15vw; /* Reduce padding */
      /* height: 100vh; */
      height: auto;
    }
  
    #left-div {
      width: 100%; /* Take full width */
      padding: 5vw 0; /* Adjust padding */
    }
  
    #left-div p {
      font-size: 4vw; /* Increase font size for readability */
      width: 100%; /* Take full width */
    }
  
    #left-div h5 {
      font-size: 4vw; /* Increase font size */
      width: 100%; /* Take full width */
      padding-bottom: 5vh; /* Reduce padding */
    }
  
    #right-div {
      width: 100%; /* Take full width */
      margin-top: 5vh; /* Add spacing between left and right divs */
    }
  
    .right-elem h4 {
      font-size: 5vw; /* Increase font size */
      width: 100%; /* Take full width */
      padding-top: 3vh; /* Adjust padding */
    }
  
    .right-elem img {
      height: 10vw; /* Increase image size */
      width: 10vw;
    }
  }