.page4{
    min-height: 100vh;
    width: 100%;
    background-color: #fff;
    position: relative;
    padding-bottom: 10vh;
}
.section{
    width: 100%;
    height: 70vh;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 15vh 8vw;
    padding-bottom: 0;
}
.section-left{
    height: 100%;
    width: 25%;
    /* background-color: crimson; */
    border-top: 2px solid #000;
}
.section-left h2{
    font-size: 2vw;
    font-weight: 500;
    text-transform: uppercase;
    color: black;
    padding-top: 3vh;
    padding-bottom: 10vh;

}
.section-left p{
    font-size: 1.2vw;
    font-weight:300 ;
    color: #000;
}
.section-right{
    height: 100%;
    width: 70%;
    background-color: yellow;
    position: relative;
}
.section-right img{
    height: 100%;
    width: 100%;
    object-fit: cover;
}
.section-right video{
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    object-fit: cover;
    opacity: 0;
    transition: opacity ease 0.1s;
}


/* Mobile Responsive Styles */
@media screen and (max-width: 768px) {
    .page4 {
        margin-top: 10vh;
      padding-bottom: 6vh; /* Reduce padding */
    }
  
    .section {
      flex-direction: column; /* Stack left and right sections vertically */
      height: auto; /* Allow height to adjust based on content */
      padding: 5vh 5vw; /* Adjust padding */
    }
  
    .section-left {
      width: 100%; /* Take full width */
      border-top: 1px solid #000; /* Adjust border thickness */
      padding-bottom: 5vh; /* Reduce padding */
    }
  
    .section-left h2 {
      font-size: 6vw; /* Increase font size */
      padding-top: 2vh; /* Adjust padding */
      padding-bottom: 3vh; /* Reduce padding */
    }
  
    .section-left p {
      font-size: 3.4vw; /* Increase font size */
    }
  
    .section-right {
      width: 100%; /* Take full width */
      height: 50vh; /* Adjust height */
      margin-top: 3vh; /* Add spacing between sections */
    }
  
    .section-right img,
    .section-right video {
      height: 100%; /* Ensure media takes full height */
      width: 100%; /* Ensure media takes full width */
    }
  }